import { Link, useRouteMatch } from "react-router-dom";
import { BiArrowToLeft, BiArrowFromLeft } from "react-icons/bi";
import { useState } from "react";
import styles from "../../styles/Dashboard.module.css";

const Sidebar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const { url } = useRouteMatch();
  return (
    <div
      className={`absolute flex h-screen w-4/5 lg:w-1/6 top-0 ${styles.slide} ${
        isOpen ? "" : styles.show
      }`}
    >
      <div className="bg-red-500 flex-grow">
        <Link to={`${url}/menu`}>
          <button className="my-4 flex justify-center items-center p-1 w-full text-white hover:text-green-300">
            Menu
          </button>
        </Link>
        <Link to={`${url}/settings`}>
          <button className="my-4 flex justify-center items-center p-1 w-full text-white hover:text-green-300">
            Settings
          </button>
        </Link>
        <Link to={`${url}/manage`}>
          <button className="my-4 flex justify-center items-center p-1 w-full text-white hover:text-green-300">
            Manage
          </button>
        </Link>
        <button className="my-4 flex justify-center items-center p-1 w-full text-white hover:text-green-300 self-end ">
          Logout
        </button>
      </div>
      <button
        onClick={(e) => {
          setIsOpen(!isOpen);
        }}
        className="flex-grow-0 self-start flex justify-center items-center focus:outline-none"
      >
        {isOpen ? (
          <BiArrowToLeft className="text-2xl text-red-500" />
        ) : (
          <BiArrowFromLeft className="text-2xl text-red-500" />
        )}
      </button>
    </div>
  );
};

export default Sidebar;
