import { useState } from "react";
import { Link } from "react-router-dom";

const CreateSession = () => {
  const [hotelID, setHotelID] = useState("");

  const handleChange = (e) => {
    setHotelID(e.target.value);
  };

  return (
    <div className="flex flex-col lg:flex-row h-screen w-screen justify-center items-center">
      <input
        type="text"
        placeholder="Hotel ID"
        value={hotelID}
        onChange={handleChange}
        className="h-8 p-1 m-1 w-60 border-b-2 border-red-500 focus:outline-none text-purple-600"
      />
      <Link
        to={`/${hotelID}`}
        className="h-8 p-1 m-1 w-40 rounded-md flex justify-center items-center border-2 border-red-500 text-purple-600 hover:text-black hover:bg-red-500 "
      >
        Start session
      </Link>
    </div>
  );
};

export default CreateSession;
