import { Switch, Route, useRouteMatch } from "react-router-dom";
import { Sidebar, Manage, Menu, Settings } from "../components/Dashboard";
import { useParams } from "react-router-dom";
import useSocket from "../hooks/useSocket";
import { ReactComponent as Welcome } from "../images/welcome.svg";

const Dashboard = () => {
  let { path } = useRouteMatch();
  const { hotelID } = useParams();
  const test = useSocket(hotelID);
  return (
    <div className="relative">
      <Sidebar />
      <Switch>
        <Route exact path={`${path}`}>
          <div className="flex flex-col justify-evenly items-center h-screen">
            <Welcome className="h-32" />
            <h3 className="text-4xl text-purple-600 text-center">
              Your hotel ID is "{hotelID}"
            </h3>
            <h3 className="text-2xl text-center">
              Click on the navigation button on the top left, and set your
              details in menu and settings if you haven't already. <br />
              Head over to manage tab to manage your clients
            </h3>
          </div>
        </Route>
        <Route path={`${path}/menu`}>
          <Menu />
        </Route>
        <Route path={`${path}/settings`}>
          <Settings />
        </Route>
        <Route path={`${path}/manage`}>
          <Manage />
        </Route>
      </Switch>
    </div>
  );
};

export default Dashboard;
