import { useState } from "react";

const Menu = () => {
  const [item, setItem] = useState({
    name: "",
    price: "",
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("submit");
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setItem({ ...item, [name]: value });
  };

  return (
    <div className="flex flex-col p-8">
      <form
        onSubmit={handleSubmit}
        className="flex flex-col lg:flex-row flex-grow-0"
      >
        <input
          onChange={handleChange}
          type="text"
          className="h-8 p-1 m-1 w-60 border-b-2 border-red-500 focus:outline-none text-purple-600"
          value={item.name}
          name="name"
          placeholder="Item name"
        />
        <input
          onChange={handleChange}
          type="number"
          className="h-8 p-1 m-1 w-60 border-b-2 border-red-500 focus:outline-none text-purple-600"
          value={item.price}
          name="price"
          placeholder="Item price"
        />
        <button
          type="submit"
          className="h-8 p-1 m-1 w-40 rounded-md flex justify-center items-center border-2 border-red-500 text-purple-600 hover:text-black hover:bg-red-500"
        >
          Add
        </button>
      </form>
    </div>
  );
};

export default Menu;
